<template>
  <div class="my-subscribe">
    <div class="content" v-for="(item, i) in data" :key="i" @click="ToSrc">
      <img :src="item.src" alt="" />
      <div>
        <p class="title">{{ item.name }}</p>
        <p class="teacher">授课老师: {{ item.teacher }}</p>
        <van-button round type="info">取消订阅</van-button>
      </div>
      <div class="slider"></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      data: [
        {
          src: "https://img.yzcdn.cn/vant/cat.jpeg",
          name: "5G改变未来社会",
          teacher: "王志民"
        },
        {
          src: "https://img.yzcdn.cn/vant/cat.jpeg",
          name: "5G改变未来社会",
          teacher: "王志民"
        },
        {
          src: "https://img.yzcdn.cn/vant/cat.jpeg",
          name: "5G改变未来社会",
          teacher: "王志民"
        }
      ]
    };
  },
  methods: {
    ToSrc() {
      this.$router.push("/source/course-vedio/detail");
    }
  }
};
</script>

<style lang="less">
.my-subscribe {
  background-color: #fff;
  overflow: auto;
  height: calc(100vh - 100px);
  padding-bottom: 20px;
  img {
    width: 140px;
    height: 80px;
    border-radius: 5px;
    margin-bottom: 8px;
  }
  .content {
    margin: 15px;
    display: flex;
    justify-content: flex-start;
    border-bottom: 1px solid #ededed;
    div > p:first-child {
      font-size: 15px;
    }
    div > p {
      text-align: left;
      margin-left: 20px;
      margin-bottom: 6px;
    }
    .teacher {
      color: #bfbfbf;
    }
    .van-button {
      height: 30px;
      line-height: 0;
      background-color: #26cea1;
      border: 1px solid #26cea1;
      float: right;
    }
    .van-button--normal {
      padding: 0 8px;
    }
    .teacher,
    .title {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      width: calc(100vw - 190px);
    }
  }
  .content:last-child {
    border: none;
  }
}
</style>



