<template>
  <div class="course-review">
    <div class="search-input">
      <van-search
        placeholder="请输入搜索关键词"
        shape="round"
        v-model="svalue"
        show-action
      >
        <template #action>
          <div @click="onRefresh" style="fontSize:15px">搜索</div>
        </template>
      </van-search>
    </div>
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
      <van-list
        v-model="loading"
        :finished="finished"
        :offset="50"
        @load="LoadData"
      >
        <div class="c-main">
          <div v-for="(item, index) in dataList" :key="index">
            <div class="c-row">
              <van-image
                round
                width="1.5rem"
                height="1.5rem"
                :src="item.advatar"
                style="position: absolute;left:0"
              />
              <div style="position: absolute;left:1.8rem">
                <p>{{ item.tName }}</p>
                <p>上传时间：{{ item.date }}</p>
              </div>
              <van-button
                type="primary"
                size="small"
                class="c-btn"
                v-if="item.focus"
                @click="FocusOn(index)"
                >已关注</van-button
              >
              <!-- <van-button
                type="primary"
                size="small"
                class="c-btn"
                v-else
                @click="FocusOn(index)"
                >关注</van-button
              > -->
            </div>
            <div>
              <van-image
                :src="item.courseImg"
                alt=""
                width="100%"
                class="course-img"
                @click="ToVideo(item.id)"
                ><img :src="defaultBG" slot="error" style="width:100%"
              /></van-image>
            </div>
            <div id="c-info">
              <span>{{ item.name }}</span>
              <span>{{ item.watchNum }}次播放</span>
            </div>
          </div>
        </div>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { formatDate } from "@/utils/formatData";
export default {
  data() {
    return {
      dataList: [],
      loading: false,
      finished: false,
      refreshing: false,
      pno: 1,
      defaultBG: require("@/assets/resources.png"),
      start: "",
      end: "",
      svalue: ""
    };
  },
  mounted() {
    const dateRange = [
      new Date(Date.now() + 8.64 * Math.pow(10, 7) * -90),
      new Date()
    ];
    this.start = formatDate(dateRange[0], "yyyy-MM-dd");
    this.end = formatDate(dateRange[1], "yyyy-MM-dd");
    // this.LoadData();
  },

  methods: {
    async LoadData() {
      if (this.refreshing) {
        this.refreshing = false;
      }
      const res = await this.$mainHttp.ElaborateCourses.GetECDatas({
        key: this.svalue,
        psize: 6,
        pno: this.pno,
        stime: this.start,
        etime: this.end,
        sKey: "",
        type: ""
      });
      if (res.Result) {
        this.pno++;
        let url = this.$mainHttp.common.downloadURL();
        res.Datas.forEach(ele => {
          if (ele.Info.ECInfo) {
            ele.Info.ECInfo = JSON.parse(ele.Info.ECInfo);
          }
          if (ele.Info.ECInfo.Teachers.slice(0, 1) === "[") {
            ele.Info.ECInfo.Teachers = JSON.parse(
              ele.Info.ECInfo.Teachers
            ).toString();
          }
          this.dataList.push({
            id: ele.Info.ECID,
            tName: ele.Info.ECInfo.Teachers,
            advatar: "https://img.yzcdn.cn/vant/cat.jpeg",
            date: ele.Info.ECInfo.RecTime,
            watchNum: ele.Info.Visits,
            name: ele.Info.ECTitle,
            courseImg: `${url}?filename=${ele.MaterialID}&fileType=9`,
            focus: false
          });
        });
      }
      this.loading = false;
      this.svalue = "";
      if (!res || !res.Datas) {
        res.RowsCount = 0;
      }
      if (this.dataList.length >= res.RowsCount) {
        this.finished = true;
      }
    },
    FocusOn(i) {
      this.dataList[i].focus = true;
    },
    ToVideo(eid) {
      this.$router.push({ name: "sourceRecorded", params: { id: eid } });
    },
    onRefresh() {
      this.finished = false;
      this.loading = true;
      this.dataList = [];
      this.pno = 1;
      this.LoadData();
    }
  }
};
</script>

<style lang="less">
.course-review {
  .van-pull-refresh {
    height: calc(100vh - 100px);
    overflow: auto;
  }
  .van-list__finished-text {
    position: relative;
    top: -40px;
  }
  .c-main {
    margin-bottom: 30px;
    > div {
      background: #fff;
      padding: 15px;
      margin-bottom: 16px;
    }
    .c-row {
      position: relative;
      height: 70px;
      div > p {
        font-size: 15px;
        font-weight: bold;
        text-align: left;
        margin-top: 5px;
      }
      div > p:last-child {
        font-size: 13px;
        font-weight: normal;
        color: #999999;
        margin-top: 2px;
      }
      .c-btn {
        position: absolute;
        right: 0;
        top: 20px;
        background: #00cbab;
        border-radius: 5px;
        border: 1px solid #00cbab;
      }
    }
    #c-info > span {
      font-size: 14px;
      font-weight: bold;
    }
    #c-info > span:last-child {
      font-size: 12px;
      font-weight: normal;
      color: #999999;
      margin-top: 2px;
    }
    #c-info {
      display: flex;
      justify-content: space-between;
    }
    #c-info > span:first-child {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      width: 65vw;
      text-align: left;
    }
    .course-img {
      border-radius: 5px;
      margin-bottom: 6px;
      height: 200px;
    }
  }
}
</style>



