<template>
  <div class="source">
    <van-tabs
      v-model="tabSelected"
      sticky
      color="rgba(0, 203, 171, 1)"
      :ellipsis="false"
      title-inactive-color="#aaa"
      :line-height="2"
      @click="handle"
    >
      <van-tab
        v-for="(item, index) in tabOptions"
        :key="index"
        :title="item.title"
        style="flex-basis: 22%;"
      >
        <recent-view v-if="item.value === 1"></recent-view>
        <MyCollection v-else-if="item.value === 2"></MyCollection>
        <course-suggest
          v-else-if="item.value === 3"
          :isPause="isPause"
        ></course-suggest>
        <course-live v-else-if="item.value === 4"></course-live>
        <course-review v-else-if="item.value === 5"></course-review>
        <course-source v-else-if="item.value === 6"></course-source>
      </van-tab>
    </van-tabs>

    <div class="tab_right" @click="toMenu">
      <van-icon class-prefix="iconfont icon-caidanguanli" size="30"></van-icon>
    </div>
  </div>
</template>

<script>
import RecentView from "./components/RecentView";
import MySubscribe from "./components/MySubscribe";
import CourseSuggest from "./components/CourseSuggest";
import CourseLive from "./components/CourseLive";
import CourseReview from "./components/CourseReview";
import CourseSource from "./components/CourseSource";
import MyCollection from "./components/MyCollection";

export default {
  components: {
    RecentView,
    MySubscribe,
    CourseSuggest,
    CourseLive,
    CourseReview,
    CourseSource,
    MyCollection
  },
  data() {
    return {
      tabOptions: [
        { title: "最近看过", value: 1 },
        { title: "我的收藏", value: 2 },
        { title: "推荐课程", value: 3 },
        { title: "直播课程", value: 4 },
        { title: "课程回看", value: 5 }
        // { title: "课件资源", value: 6 }
      ],
      tabSelected: 2,
      isPause: false
    };
  },
  mounted() {
    if (localStorage.getItem("tabSelected")) {
      this.tabSelected = Number(localStorage.getItem("tabSelected"));
    }
    this.GetConfig();
  },
  methods: {
    toMenu() {
      this.$router.push("/source/classification");
    },
    handle() {
      localStorage.setItem("tabSelected", this.tabSelected);
      if (this.tabSelected === 2) {
        this.isPause = false;
      } else {
        this.isPause = true;
      }
    },
    async GetConfig() {
      const res = await this.$mainHttp.common.serverConfigInfoGet();
      if (res) {
        this.$root.SrvConfigInfo = res.Datas;
      }
    }
  }
};
</script>

<style lang="less" scoped>
@import url("./../../styles/common/base.less");
.source {
  /deep/ .van-tabs--line .van-tabs__wrap {
    width: calc(100vw - 60px);
    .van-tab {
      padding: 0 15px;
      .van-tab__text {
        font-size: @font-sub-title + 1;
      }
    }
  }
  .tab_right {
    position: fixed;
    top: 0;
    right: 0;
    background: #fff;
    width: 60px;
    height: 44px;
    line-height: 44px;
    margin: 0 auto;
    z-index: 999;
  }
}
</style>



