<template>
  <div class="recent-view ">
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
      <van-list
        v-model="loading"
        :finished="finished"
        :offset="50"
        @load="LoadData"
        class="display-wrap"
      >
        <div
          v-for="(item, i) in rData"
          :key="i"
          class="main-live"
          @click="ToVideo(item.id)"
        >
          <div name="text">
            <van-image :src="item.videoSrc">
              <img :src="BG" alt="" slot="error" />
            </van-image>
          </div>
          <div class="text-title">
            <p>{{ item.name }}</p>
            <p>{{ item.watchNum }}次播放</p>
          </div>
        </div>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
export default {
  data() {
    return {
      rData: [],
      loading: false,
      finished: false,
      refreshing: false,
      pno: 1,
      BG: require("@/assets/resources.png")
    };
  },
  mounted() {
    // if (this.module === "recent") this.LoadData();
    // else this.LoadFavs();
  },
  methods: {
    async LoadData() {
      if (this.refreshing) {
        this.refreshing = false;
      }
      const res = await this.$mainHttp.ElaborateCourses.GetLatelyDatas({
        psize: 20,
        pno: this.pno
      });
      if (res.Datas) {
        this.pno = this.pno + 1;
        let url = this.$mainHttp.common.downloadURL();
        res.Datas.forEach(item => {
          this.rData.push({
            videoSrc: `${url}?filename=${item.MaterialID}&fileType=9`,
            watchNum: item.Info.Visits,
            name: item.Info.ECTitle,
            id: item.Info.ECID
          });
        });
      }
      if (!res || !res.Datas) {
        res.RowsCount = 0;
      }
      this.loading = false;
      if (this.rData.length >= res.RowsCount) {
        this.finished = true;
      }
    },
    onRefresh() {
      this.finished = false;
      this.loading = true;
      this.rData = [];
      this.pno = 1;
      this.LoadData();
    },
    ToVideo(eid) {
      this.$router.push({ name: "sourceRecorded", params: { id: eid } });
    }
  }
};
</script>

<style lang="less">
.recent-view {
  background-color: #fff;
  padding: 15px;
  margin-bottom: 18px;
}
@import url("../../../styles/components/liveRev");
</style>



