<template>
  <div class="course-suggest">
    <PlayBox ref="PlayBox" :Videos.sync="videoSrc" :SelectAble="false" />
    <div class="content hot-course">
      <div class="nav">
        <i class="iconfont icon-shu" style="float:left">热门课程</i>
      </div>
      <div class="main">
        <div v-for="(item, i) in hotData" :key="i" @click="ToVideo(item.id)">
          <div name="text">
            <img :src="item.videoSrc" alt="" @error="ErrA(i)" />
          </div>
          <div class="text-title">
            <p>{{ item.name }}</p>
            <p>{{ item.watchNum }}次播放</p>
          </div>
        </div>
      </div>
    </div>
    <div class="content hot-course " v-for="(item, i) in randoms" :key="i">
      <div class="nav">
        <i class="iconfont icon-shu" style="float:left">{{ item.label }}</i>
        <!-- <p>
          <span>更多</span>
          <van-icon name="arrow" />
        </p> -->
      </div>
      <div class="main">
        <div
          span="12"
          v-for="(a, b) in item.data"
          :key="b"
          @click="ToVideo(a.id)"
        >
          <div name="text">
            <img :src="a.imgUrl" alt="" @error="ErrB(i, b)" />
          </div>
          <div class="text-title">
            <p>{{ a.name }}</p>
            <p>{{ a.watchNum }}次播放</p>
          </div>
        </div>
      </div>
    </div>
    <!-- <div class="content hot-course subscribe">
      <div class="nav">
        <i class="iconfont icon-shu" style="float:left">我的订阅</i>
        <p>
          <span>更多</span>
          <van-icon name="arrow" />
        </p>
      </div>
      <div class="main">
        <div span="12" v-for="(item, i) in course" :key="i">
          <div name="text"></div>
          <div class="text-title">
            <p>{{ item.name }}</p>
            <p>{{ item.watchNum }}万观看人数</p>
          </div>
        </div>
      </div>
    </div> -->
    <!-- <div class="content my-source">
      <div class="nav">
        <i class="iconfont icon-shu" style="float:left">我的资源</i>
        <p>
          <span>更多</span>
          <van-icon name="arrow" />
        </p>
      </div>
      <MySource :data="source" />
    </div> -->
  </div>
</template>

<script>
import { formatDate } from "@/utils/formatData";
export default {
  props: ["isPause"],
  components: {
    MySource: () => import("./MySource"),
    PlayBox: () => import("../../../components/Player/PlayBox")
  },
  data() {
    return {
      hotData: [],
      videoSrc: "",
      courseList: [],
      randoms: [],
      down: ""
    };
  },
  watch: {
    isPause(val) {
      if (val) this.$refs.PlayBox.Pause();
    }
  },
  mounted() {
    const dateRange = [
      new Date(Date.now() + 8.64 * Math.pow(10, 7) * -30),
      new Date()
    ];
    this.start = formatDate(dateRange[0], "yyyy-MM-dd");
    this.end = formatDate(dateRange[1], "yyyy-MM-dd");
    this.down = this.$mainHttp.common.downloadURL();
    this.LoadData();
    this.GetCatory();
  },
  methods: {
    ErrB(i, a) {
      this.randoms[i].data[a].imgUrl = require("@/assets/resources.png");
    },
    ErrA(i) {
      this.hotData[i].videoSrc = require("@/assets/resources.png");
    },
    GoLessonDetail() {
      this.$router.push("/source/lesson-detail");
    },
    async LoadData() {
      const res = await this.$mainHttp.ElaborateCourses.GetECDatas({
        key: "",
        psize: 6,
        pno: this.pno,
        stime: this.start,
        etime: this.end,
        sKey: "",
        type: ""
      });
      if (res.Result) {
        this.pno++;
        this.LoadVideo(res.Datas[0].Info.ECID);
        res.Datas.forEach((ele, i) => {
          if (ele.Info.ECInfo) {
            ele.Info.ECInfo = JSON.parse(ele.Info.ECInfo);
          }
          if (i < 6) {
            this.hotData.push({
              id: ele.Info.ECID,
              watchNum: ele.Info.Visits,
              name: ele.Info.ECTitle,
              videoSrc: `${this.down}?filename=${ele.MaterialID}&fileType=9`
            });
          }
        });
      }
    },
    async LoadVideo(id) {
      const prefxx = `http://${this.$root.SrvConfigInfo.VODHost}:${this.$root.SrvConfigInfo.VODPort}`;
      const res = await this.$mainHttp.ElaborateCourses.GetData({
        ecid: id
      });
      if (res && res.Materials) {
        this.videoSrc = `${prefxx}/vod/${res.Materials[0].MaterialStorePath}`;
      }
    },
    // 获取条目
    async GetCatory() {
      this.randoms = [];
      const res = await this.$mainHttp.Metadata.GetTrees({ code: "JPKCLM" });
      if (res.Result) {
        for (let i = 0; i < res.Datas.length; i++) {
          const element = res.Datas[i];
          if (element.MetadataCode !== "JPKCLM") {
            const dataRes = await this.$mainHttp.ElaborateCourses.GetECDatas({
              key: "",
              psize: 6,
              pno: this.pno,
              stime: this.start,
              etime: this.end,
              sKey: "",
              type: element.MetadataCode
            });
            let arr = [];
            if (dataRes && dataRes.Datas) {
              dataRes.Datas.forEach(data => {
                arr.push({
                  id: data.Info.ECID,
                  imgUrl: `${this.down}?filename=${data.MaterialID}&fileType=9`,
                  name: data.Info.ECTitle,
                  watchNum: data.Info.Visits
                });
              });
            }
            this.randoms.push({
              value: element.MetadataCode,
              label: element.MetadataName,
              data: arr
            });
          }
        }
      }
    },
    ToVideo(eid) {
      this.$router.push({ name: "sourceRecorded", params: { id: eid } });
    }
  }
};
</script>

<style lang="less">
.course-suggest {
  padding-bottom: 20px;
  #play-box {
    margin-top: 6px;
  }
  .content {
    padding: 15px;
    margin-bottom: 12px;
    background-color: #fff;
  }
  .main {
    display: flex;
    flex-wrap: wrap;
  }
  .main > div {
    margin-left: 20px;
    margin-bottom: 16px;
  }
  .text-title {
    p {
      text-align: left;
      font-size: 14px;
      margin-top: 5px;
      width: 100%;
    }
    p:last-child {
      font-size: 12px;
      color: #999999;
    }
  }
  .nav {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    .icon-shu:before {
      color: rgb(0, 203, 171);
    }
    p span,
    p i {
      font-size: 14px;
    }
    p {
      margin-top: 2px;
      color: #999999;
    }
    p i {
      position: relative;
      top: 2px;
    }
  }
  .hot-course {
    div[name="text"] img {
      height: calc(30vw - 15px);
      border-radius: 5px;
      width: calc(50vw - 25px);
    }
    .main > div:nth-child(2n + 1) {
      margin-left: 0px;
    }
    .text-title p {
      width: calc(50vw - 25px);
    }
  }
}
</style>



