<template>
  <div class="course-live" id="liveC">
    <van-tabs v-model="active" title-active-color="#3399FE" color="#3399FE" @click="TabClick">
      <van-tab title="正在直播"></van-tab>
      <van-tab title="即将开始"></van-tab>
      <van-tab title="直播计划"></van-tab>
      <van-tab title="已结束"></van-tab>
    </van-tabs>
    <div v-if="active == 0" class="display-wrap">
      <div
        v-for="(item, index) in liveList"
        :key="index"
        @click="ToPage(item.LiveTaskID)"
        class="main-live living"
      >
        <div name="text">
          <van-image class="img" :src="item.imgUrl">
            <img :src="liveBG" slot="error" />
          </van-image>
        </div>
        <div class="text-title">
          <p class="live-name">
            {{
              item.ScheduleInfo && item.ScheduleInfo.CourseName
                ? item.ScheduleInfo.CourseName
                : item.ClsRoomName
            }}
          </p>
          <p v-if="item.ScheduleInfo && item.ScheduleInfo.Teachers">
            <span style="color:#3399FE">{{ item.ScheduleInfo.Teachers.toString() }}</span>
            <span style="float:right">{{ item.LiveStartTime | formatDay }}</span>
          </p>
          <p v-else>
            <span style="color:#3399FE"> {{ item.LiveStartTime | formatDay }}</span>
            <span style="float:right"
              >{{ item.LiveStartTime | formatMinutesTime }} -
              {{ item.LiveEndTime | formatMinutesTime }}</span
            >
          </p>
        </div>
      </div>
    </div>
    <div v-if="active == 1 && !showDetail" class="display-wrap">
      <div
        v-for="(item, index) in EvalList"
        :key="index"
        @click="ToDetail(item.LiveID)"
        class="main-live eval"
      >
        <div name="text">
          <img :src="evalBG" alt="" width="100%" />
        </div>
        <div class="text-title">
          <p>
            {{
              item.ScheduleInfo && item.ScheduleInfo.CourseName
                ? item.ScheduleInfo.CourseName
                : item.RBConfigInfo.ClsRoom.ClsRoomName
            }}
            <i
              @click.stop="Remind(item, item.LiveID, null)"
              :class="item.LRID ? 'iconfont icon-dingyue1' : 'iconfont icon-dingyue'"
              style="float:right;color:#F39424"
            />
          </p>
          <p v-if="item.ScheduleInfo && item.ScheduleInfo.Teachers">
            <span style="color:#3399FE">{{ item.ScheduleInfo.Teachers.toString() }}</span>
            <span style="float:right">{{ item.LiveStart | formatDay }}</span>
          </p>
          <p v-else>
            <span style="color:#3399FE"> {{ item.LiveStart | formatDay }}</span>
            <span style="float:right"
              >{{ item.LiveStart | formatMinutesTime }} -
              {{ item.LiveEnd | formatMinutesTime }}</span
            >
          </p>
        </div>
      </div>
    </div>
    <div v-if="active == 2">
      <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
        <van-list
          v-model="loading"
          :finished="finished"
          :offset="50"
          @load="LoadLivePlan"
          class="display-wrap"
        >
          <div v-for="(item, index) in livePlan" :key="index" class="main-live plan">
            <div name="text">
              <img :src="planBG" alt="" width="100%" />
            </div>
            <div class="text-title plan">
              <p>
                『{{ item.objectName }}』
                <span v-if="item.RPType == 1">老师的直播</span>
                <span v-if="item.RPType == 2">课程的直播</span>
                <span v-if="item.RPType == 4">班级的直播</span>
                <span v-if="item.RPType == 6">教室的直播</span>
              </p>
              <p>
                <span style="color:#3399FE"> {{ item.RPStart | formatDay }}</span>
                <i
                  @click="Remind(item, null, item.RPID)"
                  :class="item.LRID ? 'iconfont icon-dingyue1' : 'iconfont icon-dingyue'"
                  style="float:right;color:#F39424"
                />
              </p>
            </div>
          </div>
        </van-list>
      </van-pull-refresh>
    </div>
    <div v-if="active == 3">
      <van-pull-refresh v-model="refreshingA" @refresh="onRefreshA">
        <van-list
          v-model="loadingA"
          :finished="finishedA"
          :offset="50"
          @load="Loadover"
          class="display-wrap"
        >
          <div v-for="(item, index) in liveOver" :key="index" class="main-live">
            <div name="text">
              <img :src="overBG" alt="" width="100%" />
            </div>
            <div class="text-title">
              <p>
                {{
                  item.ScheduleInfo && item.ScheduleInfo.CourseName
                    ? item.ScheduleInfo.CourseName
                    : item.RBConfigInfo.ClsRoom.ClsRoomName
                }}
              </p>
              <p v-if="item.ScheduleInfo && item.ScheduleInfo.Teachers">
                <span style="color:#3399FE">{{ item.ScheduleInfo.Teachers.toString() }}</span>
                <span style="float:right">{{ item.LiveStart | formatDay }}</span>
              </p>
              <p v-else>
                <span style="color:#3399FE"> {{ item.LiveStart | formatDay }}</span>
                <span style="float:right"
                  >{{ item.LiveStart | formatMinutesTime }} -
                  {{ item.LiveEnd | formatMinutesTime }}</span
                >
              </p>
            </div>
          </div>
        </van-list>
      </van-pull-refresh>
    </div>
    <div v-if="showDetail" class="detail">
      <div>{{ detailData.LiveStart | formatDateTime }} 开始直播</div>
      <div style="background:#3399fe">
        <p>
          {{
            detailData.ScheduleInfo && detailData.ScheduleInfo.CourseName
              ? detailData.ScheduleInfo.CourseName
              : detailData.ClsRoomName
          }}
        </p>
        <p>主讲老师：{{ detailData.t }}</p>
        <p>
          {{ detailData.LiveStart | formatMM }} ~
          {{ detailData.LiveEnd | formatMM }}
        </p>
      </div>
      <div style="background:#2E2E2E">距离直播开始还有： <span v-html="countDownTime"></span></div>
    </div>
  </div>
</template>

<script>
import { formatDate, formatDiff } from "@/utils/formatData"
export default {
  data() {
    return {
      active: 0,
      liveList: [],
      livePlan: [],
      liveBG: require("@/assets/onlive.png"),
      planBG: require("@/assets/resources.png"),
      evalBG: require("@/assets/liveStart.png"),
      overBG: require("@/assets/liveOver.png"),
      EvalList: [],
      liveOver: [],
      dateRange: "",
      pagePlan: 1,
      pageOver: 1,
      pageSize: 20,
      loading: false,
      finished: false,
      refreshing: false,
      loadingA: false,
      finishedA: false,
      refreshingA: false,
      IsHubOn: false,
      showDetail: false,
      detailData: "",
      timer: 0,
      timerGO: null,
      countDownTime: "",
      LiveStartTime: "",
      down: ""
    }
  },
  mounted() {
    this.down = this.$mainHttp.common.downloadURL()
    this.dateRange = [new Date(Date.now()), new Date(Date.now() + 8.64 * Math.pow(10, 7) * 7)]
    this.LoadData()
    this.LoadEvalData()
    // this.LoadLivePlan();
    // this.Loadover();
    this.ListenTimer = window.setInterval(() => {
      this.Listen()
    }, 1000)
  },
  beforeDestroy() {
    window.onresize = null
    if (window.hub) {
      window.hub.off("ReceiveRBStatus", null)
      console.log("释放监听")
    }
    window.clearInterval(this.timer)
    this.timerGO = null
    this.timer = null
  },
  methods: {
    Listen() {
      if (!this.IsHubOn) {
        if (window.hub) {
          this.IsHubOn = true
          const $this = this
          window.hub.on("ReceiveRBStatus", function(jsonInfo) {
            var rbInfo = JSON.parse(jsonInfo)
            // console.log(rbInfo);
            if (rbInfo && rbInfo.ClsRoomID) {
              if ($this.liveList) {
                const findRB = $this.liveList.findIndex(rb => rb.ClsRoomID === rbInfo.ClsRoomID)

                if (rbInfo.LiveEstate === 1) {
                  if (findRB < 0) {
                    if (rbInfo.ScheduleInfo) {
                      const ScheduleInfo = JSON.parse(rbInfo.ScheduleInfo)
                      $this.$set(rbInfo, "ScheduleInfo", ScheduleInfo)
                      if (ScheduleInfo.Teachers.slice(0, 1) === "[") {
                        this.$set(ScheduleInfo, "Teachers", JSON.parse(ScheduleInfo.Teachers))
                      }
                    }
                    $this.liveList.push(rbInfo)
                  }
                } else {
                  if (findRB >= 0) {
                    $this.liveList.splice(findRB, 1)
                  }
                }
              }
              if ($this.EvalList) {
                const findRB = $this.EvalList.findIndex(el => el.LiveID === rbInfo.LiveTaskID)
                if (findRB >= 0) {
                  $this.EvalList.splice(findRB, 1)
                }
              }
            }
          })
        }
      }
    },
    // 订阅
    async Remind(item, tid, pid) {
      if (item.LRID) {
        const res = await this.$mainHttp.LiveTask.DelLiveRemind({
          lrid: item.LRID
        })
        if (res) {
          item.LRID = ""
        }
      } else {
        const res = await this.$mainHttp.LiveTask.AddLiveRemind({
          liveTaskId: tid,
          livePlanId: pid
        })
        if (res) {
          item.LRID = res.LRID
        }
      }
    },
    async LoadData() {
      const res = await this.$mainHttp.RBConfig.GetLiving()
      if (res && res.Datas) {
        res.Datas.forEach(rb => {
          if (rb.ScheduleInfo) {
            const ScheduleInfo = JSON.parse(rb.ScheduleInfo)
            this.$set(rb, "ScheduleInfo", ScheduleInfo)
            if (ScheduleInfo.Teachers && ScheduleInfo.Teachers.slice(0, 1) === "[") {
              this.$set(ScheduleInfo, "Teachers", JSON.parse(ScheduleInfo.Teachers))
            }
          }
          this.$set(rb, "imgUrl", `${this.down}?filename=${rb.LiveTaskID}&fileType=101`)
        })
        this.liveList = res.Datas
      }
    },
    /** 获取即将开始的数据 */
    async LoadEvalData() {
      this.EvalList = []
      const res = await await this.$mainHttp.LiveTask.GetEvalTasks()
      if (res && res.Data) {
        res.Data.forEach(lv => {
          if (lv.ScheduleInfo) {
            const ScheduleInfo = JSON.parse(lv.ScheduleInfo)
            this.$set(lv, "ScheduleInfo", ScheduleInfo)
            if (ScheduleInfo.Teachers.slice(0, 1) === "[") {
              this.$set(ScheduleInfo, "Teachers", JSON.parse(ScheduleInfo.Teachers))
            }
            if (lv.RBConfigInfo) {
              this.$set(lv, "RBConfigInfo", JSON.parse(lv.RBConfigInfo))
            }
          }
        })
        this.EvalList = res.Data
      }
    },
    /** 获取直播计划的数据 */
    async LoadLivePlan() {
      if (this.refreshing) {
        this.refreshing = false
      }
      const start = formatDate(this.dateRange[0], "yyyy-MM-dd")
      const end = formatDate(this.dateRange[1], "yyyy-MM-dd")
      const res = await this.$mainHttp.LiveTask.GetLivePlan({
        start: start,
        end: end,
        pno: this.pagePlan,
        psize: this.pageSize
      })
      if (res && res.Datas) {
        this.pagePlan++
        res.Datas.forEach(data => {
          this.livePlan.push(data)
        })
      }
      if (!res || !res.Datas) {
        res.RowsCount = 0
      }
      this.loading = false
      if (this.livePlan.length >= res.RowsCount) {
        this.finished = true
      }
    },
    async Loadover() {
      if (this.refreshingA) {
        this.refreshingA = false
      }
      const start = formatDate(this.dateRange[0], "yyyy-MM-dd")
      const end = formatDate(this.dateRange[1], "yyyy-MM-dd")
      const res = await this.$mainHttp.LiveTask.GetOverLive({
        start: start,
        end: end,
        pno: this.pageOver,
        psize: this.pageSize
      })
      if (res && res.Datas) {
        this.pageOver++
        res.Datas.forEach(data => {
          if (data.ScheduleInfo) {
            const ScheduleInfo = JSON.parse(data.ScheduleInfo)
            this.$set(data, "ScheduleInfo", ScheduleInfo)
            if (ScheduleInfo.Teachers.slice(0, 1) === "[") {
              this.$set(ScheduleInfo, "Teachers", JSON.parse(ScheduleInfo.Teachers))
            }
          }
          if (data.RBConfigInfo) {
            this.$set(data, "RBConfigInfo", JSON.parse(data.RBConfigInfo))
          }
          this.liveOver.push(data)
        })
      }
      if (!res || !res.Datas) {
        res.RowsCount = 0
      }
      this.loadingA = false
      if (this.liveOver.length >= res.RowsCount) {
        this.finishedA = true
      }
    },
    onRefresh() {
      this.finished = false
      this.loading = true
      this.livePlan = []
      this.pagePlan = 1
      this.LoadLivePlan()
    },
    onRefreshA() {
      this.finishedA = false
      this.loadingA = true
      this.liveOver = []
      this.pageOver = 1
      this.Loadover()
    },
    TabClick() {
      this.showDetail = false
      //  switch (this.active) {
      //   case 0:
      //     this.LoadData();
      //     break;
      //   case 1:
      //     this.LoadEvalData();
      //     break;
      //   case 2:
      //     this.pagePlan = 1;
      //     this.LoadLivePlan();
      //     break;
      //   case 3:
      //     this.pageOver = 1;
      //     this.Loadover();
      //     break;
      // }
    },
    async ToDetail(id) {
      this.showDetail = true
      const res = await this.$mainHttp.LiveTask.GetEvalTaskInfo({ lid: id })
      if (res && res.Datas) {
        let data = res.Datas[0].Task
        if (data) {
          const ScheduleInfo = JSON.parse(data.ScheduleInfo)
          this.$set(data, "ScheduleInfo", ScheduleInfo)
          if (ScheduleInfo.Teachers.slice(0, 1) === "[") {
            this.$set(ScheduleInfo, "Teachers", JSON.parse(ScheduleInfo.Teachers))
          }
        }
        this.detailData = data
        this.detailData.t = data.ScheduleInfo.Teachers.toString()
      }
      this.countDownTimer()
    },
    ToPage(id) {
      let name = document.querySelector(".live-name").textContent
      this.$router.push({
        path: "/source/live",
        query: { id: id, name: name }
      })
    },
    countDownTimer() {
      // 创建一个定时器
      this.timer = setInterval(() => {
        this.countDown()
      }, 1000)
    },
    countDown() {
      const start = new Date()
      let end = this.detailData.LiveStart
      end = end.slice(6, -2)
      const result = formatDiff(start, new Date(Number(end)))
      if (result.h <= 0 && result.m <= 0 && result.s <= 0) {
        this.countDownTime = "直播开始了,准备跳转~~"
        this.timerGO = setTimeout(() => {
          this.$router.push({
            path: `/source/live/${this.detailData.LiveID}`
          })
        }, 5000)
      } else {
        this.countDownTime = `<em>${result.h}</em>时<em>${result.m}</em>分<em>${result.s}</em>秒`
      }
    }
  }
}
</script>

<style lang="less">
.course-live {
  overflow: auto;
  background-color: #fff;
  padding: 15px;
  margin-bottom: 12px;
  .van-tabs--line {
    width: 100%;
    margin-top: -15px;
    margin-bottom: 12px;
  }
  @import url("../../../styles/components/liveRev");
  .text-title p {
    margin-top: 0px;
  }

  .text-title p:last-child {
    margin-top: 5px;
  }
  .text-title p:first-child span {
    margin-left: -10px;
  }
  .text-title {
    border: 1px solid #ddd;
    border-top: none;
    padding: 4px;
  }
  #plan-c {
    font-size: 0.32rem;
    color: #3399fe;
  }
}
#liveC {
  .van-tabs--line .van-tabs__wrap .van-tab {
    padding: 0px;
    margin-right: 2px;
  }
  .van-hairline--top-bottom::after,
  .van-hairline-unset--top-bottom::after {
    border-width: 0px;
  }
  .van-tab__text {
    font-size: 14px;
  }
  .detail p,
  .detail div {
    font-size: 15px;
    color: #fff;
    padding: 15px;
  }
  .detail {
    background: #666564;
    border-radius: 5px;
  }
  .detail div:last-child {
    border-radius: 5px;
    text-align: left;
  }
  div[name="text"] img {
    border-radius: 0;
  }
  .text-title {
    margin-top: -4px;
  }
  .van-image__error {
    position: static;
  }
}
</style>



