<template>
  <div class="course-source">
    <div class="filter-list">
      <van-dropdown-menu active-color="#00CBAB">
        <van-dropdown-item v-model="type" :options="typeList" title="类型" />
      </van-dropdown-menu>
      <van-dropdown-menu active-color="#00CBAB">
        <van-dropdown-item
          v-model="format"
          :options="formatList"
          title="格式"
        />
      </van-dropdown-menu>
      <van-dropdown-menu active-color="#00CBAB">
        <van-dropdown-item
          v-model="sorting"
          :options="sortingList"
          title="排序"
        />
      </van-dropdown-menu>
      <div name="filter-btn">
        <span class="sider">|</span>
        <van-icon name="filter-o" />
        <span
          @click="
            () => {
              this.show = true;
            }
          "
          >筛选</span
        >
      </div>
    </div>
    <MySource :data="source" />
    <van-popup v-model="show" round position="right" closeable>
      <div class="poup-filter type">
        <p>类型</p>
        <button
          v-for="item in typeList"
          :key="item.value"
          @click="ToActive(item.value, 'iType')"
          :class="[iType == item.value ? 'btn-active' : '']"
        >
          {{ item.text }}
        </button>
      </div>
      <div class="poup-filter">
        <p>格式</p>
        <button
          v-for="item in formatList"
          :key="item.value"
          @click="ToActive(item.value, 'iFormat')"
          :class="[iFormat == item.value ? 'btn-active' : '']"
        >
          {{ item.text }}
        </button>
      </div>
      <div class="poup-filter">
        <p>排序</p>
        <button
          v-for="item in sortingList"
          :key="item.value"
          @click="ToActive(item.value, 'iSorting')"
          :class="[iSorting == item.value ? 'btn-active' : '']"
        >
          {{ item.text }}
        </button>
      </div>
      <div class="oprate">
        <button @click="ReSet">重置</button>
        <button @click="Submit">确定</button>
      </div>
    </van-popup>
  </div>
</template>

<script>
export default {
  components: {
    MySource: () => import("./MySource")
  },
  data() {
    return {
      show: false,
      type: "",
      iType: -1,
      iFormat: -1,
      iSorting: -1,
      typeList: [
        { text: "全部我的", value: 0 },
        { text: "教案", value: 1 },
        { text: "课件", value: 2 },
        { text: "素材", value: 3 },
        { text: "试卷", value: 4 },
        { text: "其他", value: 5 }
      ],
      format: "",
      formatList: [
        { text: "docx", value: 6 },
        { text: "ppt", value: 7 },
        { text: "mp4", value: 8 }
      ],
      sorting: "",
      sortingList: [{ text: "增序", value: 9 }, { text: "降序", value: 10 }],
      source: [
        {
          score: 5.8,
          name: "美国共和党极右化和茶党化",
          contributors: "特朗普",
          time: "2020-03-03"
        },
        {
          name: "美国共和党极右化和茶党化",
          contributors: "特朗普",
          time: "2020-03-03",
          score: 5.8
        }
      ]
    };
  },
  methods: {
    ToActive(val, filed) {
      this[filed] = val;
    },
    ReSet() {
      this.iSorting = -1;
      this.iType = -1;
      this.iFormat = -1;
    },
    // 选择确定
    Submit() {}
  }
};
</script>

<style lang="less">
.course-source {
  background-color: #fff;
  .filter-list {
    display: flex;
    justify-content: space-around;
    background: #f4f4f4;
    .sider {
      font-size: 20px;
      color: #dddddd;
    }
  }
  .van-dropdown-menu {
    background: #f4f4f4;
  }
  .van-dropdown-menu__title {
    font-size: 16px;
  }
  div[name="filter-btn"] {
    font-size: 16px;
    margin-top: 16px;
    i {
      font-size: 18px;
    }
    span {
      position: relative;
      top: -4px;
      left: -5px;
    }
  }
  #my-souce-1 {
    padding-bottom: 12px;
    margin-top: 18px;
  }
  .van-popup--right {
    width: 320px;
    height: 100vh;
    overflow: auto;
  }
  .poup-filter {
    margin-bottom: 15px;
    text-align: left;
    p {
      font-size: 16px;
      font-weight: bold;
      text-align: left;
      margin-bottom: 10px;
      margin-left: 15px;
      margin-top: 15px;
    }
    button {
      padding: 8px 15px;
      border-radius: 19.5px;
      font-size: 14px;
      margin-left: 15px;
      margin-bottom: 10px;
      width: 90px;
      border: 1px solid #ddd;
    }
    .btn-active {
      border-color: #00cbab;
      color: #00cbab;
    }
  }
  .oprate button {
    padding: 10px 15px;
    width: 140px;
    border-radius: 19.5px;
    margin-right: 6px;
    background: #fff;
    border: 1px solid #ddd;
  }
  .oprate {
    position: fixed;
    bottom: 10px;
    left: 15px;
  }
  .oprate button:last-child {
    border: 1px solid #00cbab;
    background: #00cbab;
    color: #fff;
  }
  .van-popup__close-icon {
    color: #333;
  }
  .type p {
    margin-top: 36px;
  }
}
</style>



